<template>
  <div>
    <ContactProfilMobile v-if="isMobile" :item="infos"></ContactProfilMobile>
    <ContactProfil v-else :item="infos" :isSticky="isSticky"></ContactProfil>
  </div>
</template>

<script>
  // @ is an alias to /src
  import ContactProfil from '../components/ContactProfil.vue'
  import ContactProfilMobile from '../components/ContactProfilMobile.vue'
  import axios from 'axios'

  export default {
    name: 'full',
    components: {
      ContactProfil,
      ContactProfilMobile
    },
    props: {
      isMobile: Boolean
    },
    data: function () {
      return {
        infos: {},
        isSticky: true
      }
    },
    mounted () {
      axios
        .get('https://my-json-server.typicode.com/Hassancxpm/dbjson/teasers/' + this.$route.params.id)
        .then(response => (this.infos = response.data))
    },
    watch: {
      '$route' (to, from) {
        axios
          .get('https://my-json-server.typicode.com/Hassancxpm/dbjson/teasers/' + this.$route.params.id)
          .then(response => (this.infos = response.data))
      }
    }
  }
</script>
