module.exports = {
  default: {
    props: {
      item: {
        required: true,
        type: Object
      }
    },
    methods: {
      minimumOneArguments: function (arg1, arg2) {
        if ((arg1 !== null && arg1 !== '') || (arg2 !== null && arg2 !== '')) {
          return true
        }
      },
      allArgumentsDefined: function (arg1, arg2) {
        if ((arg1 !== null && arg1 !== '') && (arg2 !== null && arg2 !== '')) {
          return true
        }
      }
    },
    computed: {
      infosObject () {
        return {
          address: this.item.address ? this.item.address : '',
          country: this.item.country,
          email: this.item.email,
          phone: this.item.phone,
          portable: this.item.portable,
          website: this.item.website,
          blog: this.item.blog,
          cv: this.item.cv
        }
      }
    }
  }
}
