<template>
  <article class="ContactWrapper Profil">
    <div class="Profil-aside">
      <affix class="Profil-card" relative-element-selector=".Profil-aside" :scroll-affix="true"
             :offset="{top: offset_top, bottom: 20}" :enabled="stickyEnabled">

        <div class="Profil-image" :style="{ 'background-image': 'url(' + item.image + ')' }" v-if="item.image"></div>
        <div class="Profil-content">
          <div class="Profil-tags" v-if="item.tags">
            <div v-for="tag in item.tags" class="Tag">{{ tag }}</div>
          </div>
          <div class="Profil-actions">
            <button class="Profil-action">
              <img
                src="data:img/png;base64,"
                alt="">
              Ajouter a ma selection
            </button>
            <button class="Profil-action">
              <img
                src="data:img/png;base64,"
                alt="">
              Demande de contact
            </button>
            <button class="Profil-action">
              <img
                src="data:img/png;base64,"
                alt="">
              Imprimer une fiche
            </button>
          </div>
          <div class="Profil-more">
            <p class="Profil-stand"><span>Présent sur le stand :</span> AK 005</p>
          </div>
        </div>
      </affix>
    </div>

    <div class="ContactWrapper-main Profil-body">
      <div class="Profil-col">
        <h2 class="Profil-nameBis">{{ item.firstName }} {{ item.lastName }}</h2>
        <p class="Profil-localisation"><span>{{ item.city }}, {{ item.country }}</span></p>
      </div>

      <p class="Profil-position"><span>{{ item.position }} - {{ item.company }}</span></p>

      <div class="Profil-userTypes">
        <user-type v-for="userType in item.userTypes" :name="userType"></user-type>
      </div>

      <div class="Profil-resume"
           v-html="item.resume">
      </div>
      <ContactComplementary :infos="infosObject"></ContactComplementary>

    </div>

  </article>
</template>
<script>

  import {} from '../assets/components/profil/profil.scss'
  import {} from '../assets/components/tag/tag.scss'

  import UserType from '../components/UserType.vue'
  import ContactComplementary from '../components/ContactComplementary.vue'

  export default {
    name: 'ContactProfil',
    mixins: [require('./mixins/teaser').default],
    components: {
      UserType,
      ContactComplementary
    },
    props: {
      offset: {
        required: false,
        type: Number
      },
      isSticky: {
        required: false,
        type: Boolean
      }
    },
    data () {
      return {
        offset_top: this.offset ? this.offset : document.querySelector('header').clientHeight + 20,
        stickyEnabled: !!this.isSticky
      }
    },
    computed: {
      infosObject () {
        return {
          address: this.item.address,
          country: this.item.country,
          email: this.item.email,
          phone: this.item.phone,
          portable: this.item.portable,
          website: this.item.website,
          blog: this.item.blog,
          cv: this.item.cv
        }
      }
    },
    methods: {
      onResize: function () {
        if (this.isSticky) {
          window.innerWidth < 700 ? this.stickyEnabled = false : this.stickyEnabled = true
        }
      }
    },
    mounted () {
      window.addEventListener('resize', this.onResize)
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.onResize)
    }
  }
</script>
