<template>
  <div class="Paging">
    <button @click="$emit('currentPageMinus')" class="Paging-prev" :disabled="!!isPrevDisabled"> <span><</span> Précedent </button>

    <button v-if="current == totalPages && totalPages > 2" class="Paging-item" @click="$emit('goToPage', current - 2)">{{ current - 2 }}</button>
    <button v-if="current > 1" class="Paging-item" @click="$emit('goToPage', current - 1)">{{ current - 1 }}</button>
    <p class="Paging-item Paging-current">{{ current }}</p>
    <button v-if="current < totalPages " class="Paging-item" @click="$emit('goToPage', current + 1)">{{ current + 1 }}</button>
    <button v-if="current == 1 && current < totalPages && totalPages > 2" class="Paging-item" @click="$emit('goToPage', current + 2)">{{ current + 2 }}</button>

    <button @click="$emit('currentPagePlus')" class="Paging-next" :disabled="!!isNextDisabled"> Suivant <span>></span> </button>
  </div>
</template>

<script>
  import {} from '../../src/assets/components/paging/paging.scss'


  export default {
    name: 'Paging',
    props: {
      current: {
        required: true
      },
      totalPages: {
        required: true
      }
    },
    computed: {
      isPrevDisabled () {
        return this.current === 1
      },
      isNextDisabled () {
        return this.current === this.totalPages
      }
    }
  }
</script>
