<template>
  <article class="Teaserlist">
    <div class="Teaserlist-image" :style="{ 'background-image': 'url(' + item.image + ')' }" v-if="item.image"></div>
    <div class="Teaserlist-infos">
      <div class="Teaserlist-head">
        <!--<a class="Teaserlist-name" v-if="allArgumentsDefined(firstName, lastName)">{{ firstName }} {{ lastName }}</a>-->
        <router-link :to="{name: 'full', params: { id: item.id }}" class="Teaserlist-name"
                     v-if="allArgumentsDefined(item.firstName, item.lastName)">{{ item.firstName }} {{ item.lastName }}
        </router-link>
        <span class="Teaserlist-localisation">{{ item.city }}<span
          v-if="allArgumentsDefined(item.city, item.country)">,</span> {{ item.country }}</span>
      </div>
      <span class="Teaserlist-position" v-if="minimumOneArguments(item.position, item.company)">{{ item.position }}<span
        v-if="allArgumentsDefined(item.position, item.company)">,</span> {{ item.company }}</span>
      <div class="Teaserlist-userTypes">
        <user-type v-for="userType in item.userTypes" :name="userType"></user-type>
      </div>


      <div class="Teaserlist-resume" v-if="item.resume" v-html="cropResume"></div>


      <ul class="Teaserlist-footer" v-if="infosObject">
        <li v-if="infosObject.email">Email :  <span><a :href="`mailto:${infosObject.email}`">{{ infosObject.email }}</a></span></li>
        <li v-if="infosObject.phone">Téléphone :  <span>{{ infosObject.phone }}</span></li>
        <li v-if="infosObject.portable">Portable :  <span>{{ infosObject.portable }}</span></li>
        <li v-if="infosObject.address">Adresse :  <span>{{ infosObject.address }}</span></li>
        <li v-if="infosObject.website">Site Web :  <span><a :href="infosObject.website">{{ infosObject.website }}</a></span></li>
        <li v-if="infosObject.blog">Blog : <span><a :href="infosObject.blog">{{ infosObject.blog }}</a></span></li>
        <li v-if="infosObject.cv">CV :  <span><a :href="infosObject.cv">{{ infosObject.cv }}</a></span></li>
      </ul>
    </div>
  </article>
</template>

<script>
  import {} from '../assets/components/teaserlist/teaserlist.scss'

  import UserType from '../components/UserType.vue'

  export default {
    name: 'ContactTeaserList',
    mixins: [require('./mixins/teaser').default],
    components: {
      UserType
    },
    data () {
      return {}
    },
    computed: {
      cropResume: function () {
        if ((this.item.resume).length > 350) {
          return this.removeHtmlTag(this.item.resume.substr(0, 349) + '...')
        }
        else {
          return this.removeHtmlTag(this.item.resume)
        }
      }
    },
    methods: {
      removeHtmlTag: function (text) {
        let regex = /(<([^>]+)>)/ig
        return text.replace(regex, '')
      }
    }
  }
</script>
