<template>
  <div class="ContactWrapper">
    <aside class="ContactWrapper-aside Filters" v-if="aside">

    </aside>

    <div class="ContactWrapper-main">
      <list-contact v-if="isList" v-for="teaser in teasers" :item="teaser"></list-contact>

      <template v-if="isGrid">
      <div :class="[isGrid ? 'GridL GridM' : '' ]">
        <grid-contact v-if="variant == 'grid'" v-for="teaser in teasers" :item="teaser"></grid-contact>
      </div>
      </template>

      <Paging :current="currentPage" :totalPages="totalPages" @currentPageMinus="onCurrentPageMinus" @currentPagePlus="onCurrentPagePlus" @goToPage="onGoToPage"></Paging>
    </div>
  </div>
</template>
<script>
  import {} from '../../src/assets/styles/utilities/contactwrapper.scss'
  import {} from '../../src/assets/components/filters/filters.scss'

  import axios from 'axios'
  import ListContact from '../components/ContactTeaserList.vue'
  import GridContact from '../components/ContactTeaserGrid.vue'
  import Paging from '../components/Paging.vue'

  export default {
    name: 'ResultsContact',
    components: {
      ListContact,
      GridContact,
      Paging
    },
    props: {
      variant: String,
      aside: Boolean
    },
    data: function () {
      return {
        teasers: {},
        currentPage: 1,
        totalPages: '',
        itemsPerPage: 3
      }
    },
    mounted () {
      axios
        .get('https://my-json-server.typicode.com/Hassancxpm/dbjson/teasers?_page=' + this.currentPage + '&_limit=' + this.itemsPerPage)
        .then(response => {
          this.teasers = response.data
          this.totalPages = Math.ceil(response.headers['x-total-count'] / this.itemsPerPage)
        })
    },
    computed: {
      isGrid: function() {
        return this.variant === 'grid'
      },
      isList: function() {
        return this.variant === 'list'
      }
    },
    methods: {
      reloadData: function () {
        axios
          .get('https://my-json-server.typicode.com/Hassancxpm/dbjson/teasers?_page=' + this.currentPage + '&_limit=' + this.itemsPerPage)
          .then(response => {
            this.teasers = response.data
            this.totalPages = Math.ceil(response.headers['x-total-count'] / this.itemsPerPage)
          })
      },
      onCurrentPageMinus: function () {
        if (this.currentPage > 1) {
          this.currentPage -= 1
          this.reloadData()
        }
      },
      onCurrentPagePlus: function () {
        if (this.currentPage < this.totalPages) {
          this.currentPage += 1
          this.reloadData()
        }
      },
      onGoToPage: function (page) {
        this.currentPage = page
        this.reloadData()
      }
    }
  }
</script>
